@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Comfortaa:wght@300&display=swap');

$font-primary: 'Bebas Neue', cursive;
$font-secondary: 'Comfortaa', cursive;
$color-primary: #3A6EA5;
$color-secondary: #E8AA14; 
$color-terciary: #9B287B;
$color-text-primary: #F4FFF8;

@media all and (max-width: 425px) {
    .landpage {
        display: box;
    }

    .headline {
    	width: 100vw;
    }

    .headline-text {
    	font-size: 15rem;
    }
    .div-button {
		width: 100vw;
		margin-left: auto;
		float: none;
	}

}

div {
	outline: 1px solid hotpink;
}

.ratio img {
	transform: scaleY(-1);
	position: absolute;
	height: 100vh; 
    width: 100vw; 
}

img {
	max-height: 100%; 
	max-width: 100%; 
	object-fit: contain;
}

.landpage {
	background-color: $color-primary;
	padding-top: 1vw;
	width: 100%;
	height: 100%;
	display: flex;
	z-index: -2;
}

.headline {
	position: relative;
	width: 62vw;
	z-index: 1;
}

.headline-text {
	background-color: $color-terciary;
	font-family: $font-primary;
	font-size: 4vw;
	text-align: center;
	color: $color-text-primary;
	width: 90%;
	margin-left: auto;
	z-index: 1;
}

.middleline {
	width: 85%;
	margin-left: auto;
}

.middleline-text {
	font-size: 1.25rem;
	font-family: $font-secondary;
	color: $color-text-primary;
}

.polias {
	position: relative;
	width: 100%;
	display: flex;
}

.polia-headline1 {
	margin-left: 55%;
}

.polia-headline2 {
	margin-left: 25%;
}

.headline-rope {
	position: absolute;
	background-color: black;
	margin-top: -7vh;
	margin-left: 56%;
	width: 0.25%;
	height: 98%;
	z-index: -1;
}

.polias-rope {
	transform: rotate(270deg);
	position: absolute;
	background-color: black;
	margin-top: -20.5vw;
	margin-left: 80%;
	width: 0.25%;
	height: 30vw;
	z-index: -1;
}

.gear-headline {
	width: 10%;
	float: right;
	margin-top: -5%;
	z-index: 3;
}

.gear-button {
	width: 50%;
}

.div-button {
	width: auto;
	margin-left: auto;
	float: right;
}

.separator {
	width: 15vw;
}

.skills {
	height: 100vh;
}